<template>
    <div class="main">
      <canvas id="canvas" style="position: absolute; height: 55%; width:90%;"/>
      <div class="jinrishici-sentence">
        <span id="jinrishici-sentence">每日诗词...</span></div>
        <div class="top-bar" style="height: 30px;text-align: right;">
          <button class="top-button" @click="$router.push({path:'/setting'})" style="cursor:pointer;"><h5>设置</h5></button><!-- 绑定点击按钮 -->
        </div>
      <!-- <canvas id="canvas" ClipToBounds="True" width="500"></canvas> -->
      <button class="infomsg" @click="$router.push({path:'/info'})" style="cursor:pointer;"><h5>Moument of Version</h5></button><!-- 绑定点击按钮 -->
      <transition name="fade">
        <div class="search-input" v-show="isShow"><!-- 状态改变 -->
          <button class="click-change" placement="bottom" effect="light" :show-after="500">
            <search-engines-switch></search-engines-switch>
          </button>
        <input type="text" v-model="input" @select="toSearch" class="input-text" @keydown.down="add(1)" @keydown.up="add(-1)" @keydown.enter="toSearch"/>
        <button class="start-search" @click="toSearch" style="cursor:pointer;">Start</button>
        </div>
        </transition>
        <!-- <tools></tools> -->
        <div class="link-card">
      <div class="links" v-for="line of Math.ceil($store.state.links.length/$store.state.numOfRow)" :key="line">
        <transition-group name="slide-up">
        <link-item
            v-for="(item,index) in $store.state.links.slice($store.state.numOfRow * (line-1), $store.state.numOfRow * line)"
            :key="index" :icon-src="item.iconSrc" :title="item.title" :url="item.url" v-show="isShow"
        ></link-item>
        </transition-group>
      </div>
    </div>
    </div>
  </template>
  
  <script>
import { init } from './rain';
import {ref,onMounted} from 'vue'
import { jsonp } from 'vue-jsonp'
import {useStore} from 'vuex'
import SearchEnginesSwitch from "@/components/SearchEnginesSwitch";
import LinkItem from "@/components/LinkItem";
/*import Tools from './Tools.vue';*/
export default {
  name: "Home",
  components:{
    SearchEnginesSwitch,
    /*Tools*/
    LinkItem
  },
  methods: {
    
  },
  setup(){
    const store = useStore();

    let input = ref("");
    let isShow = ref(!store.state.enterAnimation);

    let lastFetch = [];
    let backup = "";
    let move = -1;

    const querySearchAsync = (queryString, cb) => {
      let results = [];
      lastFetch = [];
      move = -1;
      backup = input.value;
      jsonp('https://suggestion.baidu.com/su?wd='+queryString,{callbackQuery: 'cb'}).then(res=>{
        res.s.forEach(item => results.push({value: item}));
        cb(results);
        lastFetch = results;
        move = -1;
      })
    };

    const toSearch = () => {
      window.location.href = store.getters.currentSearchEngine.url + input.value
    }

    onMounted(()=>{
      setTimeout(()=>{
        isShow.value = true;
      },1)
    })

    const add = (num)=>{
      move += num;
      if(move > lastFetch.length-1){
        move = lastFetch.length-1;
      }

      if(move < 0){
        move = -1;
        input.value = backup;
      }else{
        input.value = lastFetch[move].value;
      }
    }
    return {
      input,
      querySearchAsync,
      toSearch,
      isShow,
      add,
      canvasStyle: {
        position: "fixed",
        width: "100%",
        height: "100%",
        zIndex: "-1",
        left: "0",
        bottom: "0",
      },
      ctx: {},
    }
  },
  mounted() {
    this.initCanvas();
  },
  methods: {
    initCanvas() {
      const canvas = document.querySelector("#canvas");
      this.ctx = canvas.getContext("2d");
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      init(this.ctx);
    },
  }, 
}
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.version_total{
  position: absolute;
  width: 10%;
  height: 5%;
}
*{ 
  /*background-color: #ffffff;*/
  /*background-color: #f1f5f8;*/
}.top-bar{background-color: #00000000;}
.top-button{
  position: relative;
  top: 25px;
  right: 100px;
  background-color: #00000000;
  h5{
  font-size: 15px;
  color: #ffffff;
  background-color: #00000000;
} }
  .main {
    height: 300px;
    width: 100%;
    padding: 10px 0 0 0;
    background-image: linear-gradient(to bottom right, #63bbf7, #5289fc, #9c83f7);
    /*p{
      margin: 0 0 0 25px;
      color: rgb(238, 238, 238);
      font-weight: 400;
      font-size: 20px;
      font-family: sans-serif;
      font-style: italic;
    }*/
    .jinrishici-sentence {
      background-color: #00000000;
      position: absolute;
      left: 50%;
      top: 150px;
      transform: translateX(-50%);
      height: 20px;
      font-size: 23px;
      color: #ffffff;
      cursor: pointer;
    }
    .infomsg{
      position: absolute;
      top: 8px;
      right: 25px;
      font-size: 25px;
      background-color: #ffffff00;
      /*取消四周黑边*/
      border: none;
      outline: none;
      h5{
        color: #ffffff;
        background-color: #00000000;
      }
    }
    .click-change{
      position: absolute;/*选项套在搜索框内*/
      border: none;
      outline: none;
      background-color: #ffffff00;
      margin: 10px 0 0 0;
      height: 30px;
    }
    .link-card {
      display: block;
      position: absolute;
      width: auto;
      top: 38%;
      left: 25%;
      right: 20%;
    }

  }  
  .select-option {
    background-color: #ffffff00 !important;
    border: none !important;
    outline: none !important;
    li{
      color: #000000 !important;
      font-weight: 500 !important;
    }
  }

  
  .search-input{
    position: absolute;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 50px;
    width: 400px;
    border: none;
    outline: none;
    border:rgba(46, 46, 46, 0.822) 2px solid;/*阴影*/
    border-radius: 5px 5px 5px 5px;
    background-color: #ffffff;
    .input-text{
      width: 300px;
      height: 25px;
      max-width: 1100px;
      margin: 10px 40px 10px 50px;
      font-size: 20px;
      font-weight: 600;
      color: #5c5c5c;
      border: none;
      outline: none;
      background-color: #f1f5f8;
    }
    .start-search{
      position: absolute;
      color: black;
      font-size: 16px;
      font-weight: 700;
      bottom: -1px;
      right: 5px;
      border: 0;
      background: transparent;
      height: 100%;
    }
  }.search-input:hover{ box-shadow: -0.5px 5px 5px 5px #49494934}
  .fade-enter-active{
    transition: opacity 0.8s linear;
  }
  .fade-enter-from{
    opacity: 0;
  }

  .slide-up-enter-active {
    transition: all 0.6s ease-out;
  }
  .slide-up-enter-from{
    transform: translateY(20px);
  }
  </style>